.home-1
	position: relative
	z-index: 2
	&::before
		content: ''
		position: absolute
		left: 0
		right: 0
		top: 0
		height: 40px
		background: #fff
		z-index: -1

	.product-category-wrapper
		width: 250px
		.product-category-toggle 
			background-image: url(../img/cate-bg.png)
			background-repeat: no-repeat
			background-size: cover
			padding-left: 20px
		.product-category-dropdown
			position: static
			display: block
		.product-category-list
			box-shadow: none
			position: relative

	.big-banner-promo-wrapper
		width: 725 / 1230 * 100%

	.big-banner-promo
		position: relative
		padding-left: 5px
		.img
			+img-scale(450 / 720 * 100%)

		.swiper-pagination
			bottom: 20px
			right: 20px
			left: auto
			width: auto
			.swiper-pagination-bullet
				height: 10px
				width: 10px
				background-color: transparent
				border: 2px solid #e1e1e1
				opacity: 1
				&.swiper-pagination-bullet-active
					background-color: map-get($colors, main)
		.swiper-btn
			border-radius: 0
			margin: 0
			transform: translateY(-50%)
			width: r(50px)
			height: r(50px)
			font-size: r(40px)
			color: map-get($colors, blue)
			background: rgba(#f3f3f3, 0.7)
			&.swiper-btn-prev
				right: auto
				left: 0
			&.swiper-btn-next
				right: 0
				left: auto

	.small-banner-promo-wrapper
		flex: 1
	.small-banner-promo
		padding-left: 5px
		ul
			li
				+ li
					margin-top: 5px
		.img
			+img-scale(146.5 / 250 * 100%)

	.feature-group-wrap
		padding: r(25px) 0
		.feature-group
			margin-left: -10px
			margin-bottom: -10px
		.feature-item
			padding-left: 10px
			margin-bottom: 10px
		.feature-link
			display: flex
			align-items: center
			height: r(50px)
			border-radius: 25px
			background-color: #fff
			box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
			padding: 5px
			transition: 0.3s all
			&:hover
				background-color: map-get($colors, main)
				.icon
					color: map-get($colors, main)
				.text
					color: #fff
			.icon
				height: r(44px)
				width: r(44px)
				background-color: #f8f8f8
				color: map-get($colors, blue)
				font-size: r(24px)
				display: flex
				align-items: center
				justify-content: center
				border-radius: 50%
				transition: 0.3s all
			.text
				font-size: r(18px)
				display: block
				padding-left: 10px
				font-weight: 500
				transition: 0.3s all

	@media screen and ( max-width: 1280.98px )
		.big-banner-promo 
			.img 
				padding-top: 450px
	@media screen and ( max-width: 1024.98px )
		&:before
			display: none
		.product-category-wrapper
			width: 100%
			order: 3
			overflow: hidden
			.product-category-toggle
				background-image: none
				background-color: #fff
		.big-banner-promo-wrapper
			width: 100%
			order: 1
		.big-banner-promo 
			padding-left: 0
			.img 
				padding-top: 140 / 375 * 100%
			.swiper-navigation
				display: none
		.feature-group-wrap
			order: 2
			width: 100%
		.product-category
			width: 100%
			border-top: 6px solid #ced1d8
			.product-category-subnav-container
				display: none !important
			.product-category-dropdown
				border: 0
			.product-category-list
				box-shadow: none
				padding: 15px
				> ul
					display: flex
					flex-wrap: wrap
					margin-bottom: -20px
					margin-left: -5px
					margin-right: -5px
					> li
						width: 25%
						margin-bottom: 20px
						padding: 0 5px
			.product-category-link
				border-top: 0 !important
				min-height: auto
				.text
					flex-direction: column
					width: 100%
					.icon
						width: 100%
						+img-scale(100%)
					span
						display: block
						font-size: 14px
						margin-top: 10px
						text-align: center
						line-height: 1.3
				.arrow
					display: none

		.product-category-toggle
			border-bottom: 1px solid #eeeeee
			height: 50px
			em
				display: none
			strong
				font-size: 16px
	@media screen and ( max-width: 576px )
		.container
			padding: 0
		.big-banner-promo-wrapper
			padding-top: 140 / 375 * 100%
			position: relative
			.big-banner-promo
				position: absolute
				top: 0
				left: 0
				right: 0
				bottom: 0
		.feature-group-wrap
			background: #fff
			padding: 10px 0
			.feature-link
				flex-direction: column
				background: transparent
				box-shadow: none
				height: auto
				.icon
					height: 42px
					width: 42px
					margin: 0 auto
					color: #fff
					font-size: 24px
				.text
					margin-top: 10px
					padding-left: 0
					text-align: center
			.feature-item
				&:nth-child(1)
					.feature-link
						.icon
							background-image: linear-gradient(180deg, rgba(0,153,197,1) 0%, rgba(52,209,40,1) 100%)
				&:nth-child(2)
					.feature-link
						.icon
							background-image: linear-gradient(180deg, rgba(255,155,194,0.9) 0%, rgba(240,90,148,1) 100%)
				&:nth-child(3)
					.feature-link
						.icon
							background-image: linear-gradient(180deg, rgba(255,147,172,1) 0%, rgba(255,0,0,1) 100%)
				&:nth-child(4)
					.feature-link
						.icon
							background-image: linear-gradient(180deg, rgba(255,150,0,1) 0%, rgba(255,198,0,1) 100%)
