.fixed-nav
	position: fixed
	bottom: 10vh
	right: 10px
	z-index: 22
	ul
		li
			+ li
				margin-top: 15px
			a
				width: r(50px)
				height: r(50px)
				display: flex
				align-items: center
				justify-content: center
				border-radius: 50%
				font-size: r(24px)
				&.back-to-top
					border: 2px solid map-get($colors, blue)
					color: map-get($colors, blue)
					background: #fff
				&.map
					background: linear-gradient(135deg, rgba(34,55,113,1) 10%, rgba(86,170,255,1) 100%)
					color: #fff
				&.hotline
					background: linear-gradient(135deg, rgba(188,0,73,1) 10%, rgba(240,90,148,1) 100%)
					color: #fff

.fixed-nav-mobile
	position: fixed
	bottom: 0
	left: 0
	right: 0
	z-index: 99
	background: #fff
	ul
		display: flex
		li
			width: 25%
			a
				display: flex
				align-items: center
				justify-content: center
				min-height: 60px
				flex-direction: column
				.icon
					height: 24px
					margin-bottom: 5px
					img
						max-width: 24px
					em
						font-size: 24px
						color: map-get($colors, blue)
				span
					display: block
					text-align: center
					font-size: 12px
					color: #999