
.landing-page-product-list
	margin-bottom: r(30px)
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	background: #ffffff
	.heading
		padding: r(30px) 0
	.product-list
		border-top: 1px solid #eee
		.product-item
			border-bottom: 1px solid #eee


* + .landing-page-1-item
	padding: 40px 0

div[class*="landing-page-"]
	.view-more-wrap
		padding: r(25px) 0
		text-align: center
		.btn
			height: 34px
			border-radius: 20px
			width: 130px