.side-news-list
	.row
		margin-bottom: -20px
		> *
			padding-bottom: 20px
.lastest-news-section
	@media screen and ( max-width: 1024.98px )
		padding: r(30px) 0
		
.catalouge-news-section
	padding-bottom: r(60px)
	.container
		position: relative
	.btn-view-all
		height: 34px
		width: 140px
		border-radius: 17px
	@media screen and ( min-width: 1024px )
		.btn-view-all
			position: absolute
			top: 0
			right: 15px
	@media screen and ( max-width: 1024.98px )
		padding: r(30px) 0
		.btn-view-all
			background: transparent !important
			border: 2px solid map-get($colors, main) 
			color: map-get($colors, main) !important
			margin: 20px auto 0
			display: flex
		&:nth-child(odd)
			background-color: #fff