.product-list-col
	flex: 1

.product-list
	position: relative
	&:after
		content: ''
		position: absolute
		top: 0
		width: 1px
		background: #fff
		right: 0
		bottom: 0
		z-index: 2
	@media screen and ( max-width: 1024.98px )
		.row
			margin-bottom: 0
			> *
				padding-bottom: 0
.product-list-wrap
	background-color: #fff
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

.product-list-heading
	padding: 10px 20px 0
	border-bottom: 1px solid #eeeeee
	> *
		margin-bottom: 10px
	.icon
		width: 40px
		height: 40px
		border-radius: 6px
		border: 1px solid #e5e5e5

.product-list-util
	select
		border: 0
		font-size: 1rem
		appearance: none
		background-image: url(../img/sl.png)
		background-repeat: no-repeat
		background-position: 95% center
		background-color: transparent
.product-list-sort,.product-list-display
	display: flex
	align-items: center
	.nice-select
		position: relative
		display: flex
		align-items: center
		justify-content: space-between
		cursor: pointer
		&:after
			content: "\EA4F"
			font-family: remixicon
			margin-left: 1.5rem
		&.open
			&:after
				content: "\EA55"
			.list
				display: block
		.list
			border-radius: 0
			border: 1px solid #e1e1e1
			box-shadow: none
			min-width: r(200px)
			position: absolute
			top: 100%
			right: 0
			z-index: 1
			background: #fff
			display: none
			margin-top: 10px
			li
				padding: 8px 12px
				+ li
					border-top: 1px solid #e1e1e1
			.option
				&:hover,&.selected, &.focus
					color: #fff
					background-color: map-get($colors, main)
	@media screen and ( max-width: 768.98px )
		font-size: r(18px)

.product-list-sort
	@media screen and ( max-width: 768.98px )
		.text-blue
			color: #333
			font-weight: 400
		.nice-select
			.current
				color: map-get($colors, blue)
				font-weight: 700


.product-list-display
	select
		width: 40px
	.nice-select
		.list
			min-width: auto
			width: 100%

.product-list-body
	.row
		margin-left: 0
		> *
			padding-left: 0

.product-slider
	.swiper-btn
		z-index: 20
		&.swiper-btn-prev
			right: auto
			left: 15px
			transform: translateY(-50%)
		&.swiper-btn-next
			right: 15px
			left: auto
			transform: translateY(-50%)

.product-section
	@media screen and ( max-width: 1024.98px )
		.product-list-heading
			flex-direction: column
			.product-list-title
				order: 2
			.product-list-util
				order: 1
		.filter-mobile-toggle
			margin-left: auto
			font-size: r(18px)
	@media screen and ( max-width: 576px )
		padding: 0
		.container
			padding: 0
		.product-list-heading
			padding: 0
		.product-list-util
			padding: 15px
			background-color: #f8f8f8
			background-image: url(../img/product/filter-bg.png) 
			background-repeat: no-repeat 
			background-position: 105% 15px
			border-bottom: 1px solid #e5e5e5
		.product-list-title 
			padding: 0 15px
		.product-list
			.row
				margin-bottom: 0