.checkout-header
	padding: 25px 0
	background: #fff
	.hotline
		height: 100%
		display: flex
		align-items: center
	.progress
		display: flex
		.progress-item
			position: relative
			width: 33.33%
			.text
				text-align: center
				font-size: r(18px)
			.bar
				height: 2px
				margin: 30px 0px 0
				background: #e1e1e1
				position: relative
				.fill-color
					width: 50%
					height: 100%
					background: transparent
			.circle
				position: absolute
				width: 40px
				height: 40px
				background: #fafafa
				top: 45px
				left: 50%
				margin-top: -15px
				margin-left: -15px
				border-radius: 50%
				color: #666666
				font-size: 16px
				display: flex
				align-items: center
				justify-content: center
				box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.1)
				z-index: 2
			&.done
				.bar
					.fill-color
						width: 100%
						background: map-get($colors, main)
				.circle
					background: $gradient-1
					color: #fff
			&.active
				.fill-color
					width: 100%
					background: map-get($colors, main)
				.circle
					background: $gradient-1
					color: #fff
			&:first-child
				.bar
					left: 50%
					width: 50%
			&:last-child
				.bar
					width: 50%

	@media screen and ( max-width: 1024.98px )
		background: map-get($colors, main)
		padding: 15px 0
		.progress-title-active
			text-align: center
			color: #fff
			font-weight: 700
			font-size: 20px
			margin-bottom: 10px
			position: relative
			a
				display: block
				position: absolute
				font-size: 24px
				top: 50%
				transform: translateY(-50%)
		.progress
			justify-content: center
			.progress-item
				display: flex
				align-items: center
				.text
					display: none
				.circle
					position: static
					margin-left: 0
					margin-top: 0
					background: #fff !important
					color: map-get($colors, main) !important
					opacity: 0.6
					font-size: 18px
				.bar
					margin: 0 20px
					flex: 1
					left: 0 !important
					opacity: 0.6
					.fill-color
						background: #fff !important
				&:last-child
					width: auto
					.bar
						display: none
				&.active,&.done
					.circle
						opacity: 1
					.bar
						opacity: 1

	@media screen and ( max-width: 576px )
		.progress
			.progress-item
				width: 50%