.dealer-section
	padding-bottom: 30px !important
	.row
		background: #ffffff
		box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.13)
	.result-wrapper
		padding: r(30px) 0
	.map
		width: 100%
		height: 100%
		#map_canvas
			height: 100%
			width: 100%
			img
				width: 100%
	#ajax_msg
		display: none !important
	.filter-map
		padding: 0 r(30px)
		.form-group
			margin-top: r(20px)
			select
				appearance: none
				background-image: url(../img/sl.png)
				background-repeat: no-repeat
				background-position: 97% center
				height: 40px
				width: 100%
				background-color: #f2f7fc
				border-radius: r(20px)
				border: 1px solid #ebebeb
				padding: 0 15px
				color: map-get($colors, blue)
				font-size: 14px
				font-weight: 700
		input[type=submit]
			width: r(160px)
			height: r(40px)
			border-radius: r(20px)
			background: $gradient-1
			color: #fff
			font-weight: 700
			border: 0
			cursor: pointer
	.result-list
		margin-top: r(30px)
		padding: 0 r(10px) 0 r(30px)
		ul
			max-height: 432px
			overflow-y: auto
			padding-right: r(15px)
			&::-webkit-scrollbar
				background-color: #e1e1e1
				width: 5px
				border-radius: 2px
			&::-webkit-scrollbar-thumb
				border-radius: 2px
				background-color:  map-get($colors, blue)
			li
				line-height: 1.375
				.name
					font-size: r(18px)
					font-weight: 700
					margin-bottom: 10px
				.address
					display: flex
					color: map-get($colors, light-grey)
					&:before
						font-family: remixicon
						content: "\EF09"
						margin-right: r(8px)
				.distance
					padding-left: r(24px)
					margin-top: 5px
					span
						&:after
							content: '|'
							color: #333
							margin: 0 10px
				+ li
					margin-top: r(20px)
					padding-top: r(20px)
					border-top: 1px solid #e5e5e5
				&.active
					.name
						color: map-get($colors, main)
	@media screen and ( max-width: 768.98px )
		padding: 20px 0 !important
		.section-title
			text-align: left
		.row
			background: transparent
			box-shadow: none
		.filter-map
			padding: 0
			.form-group
				select
					border-radius: 0
					background-color: #fff
		.result-wrapper
			padding-bottom: 0
		.result-list
			padding: 0
			margin-right: -5px
			ul
				li
					padding: 15px !important
					background: #fff
					border-radius: 5px
					+ li
						padding-top: 0
						margin-top: 15px
						border-top: 0px

.dealer-slider
	.img
		+img-scale(550 / 1230 * 100%)
	.swiper-btn
		margin: 0
		transform: translateY(-50%) !important
		&.swiper-btn-prev
			right: auto
			left: 15px
		&.swiper-btn-next
			left: auto
			right: 15px