.filter-col
	width: 280px
	@media screen and ( max-width: 1024.98px )
		width: 100%

.product-filter-item
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.06)
	border: 1px solid #e5e5e5
	+ .product-filter-item
		margin-top: 10px

	.product-filter-title
		height: r(50px)
		background-image: url(../img/product/filter-bg.png) 
		background-repeat: no-repeat 
		background-position: 105% 15px
		background-color: #f8f8f8
		padding: 0 10px 0 15px
		cursor: pointer
		border-bottom: 1px solid #e5e5e5
		.arrow
			transition: 0.3s all
		&.active
			color: map-get($colors, blue)
			.arrow
				transform: rotate(180deg)

	.product-filter-body
		padding: 15px 20px
		background: #fff
		display: none

	.product-filter-group
		ul
			li
				border-bottom: 2px dotted #e5e5e5
				padding-bottom: 10px
				margin-bottom: 8px
				&:nth-child(n+11)
					display: none
				.filter-link 
					line-height: 1.2
					display: flex
					.check-box
						margin-right: 5px
						position: relative
						display: inline-block
						position: relative
						top: -1px
						&:before
							font-family: remixicon
							content: "\EB7F"
					.text
						display: inline-block
						font-size: 14px
						text-transform: uppercase
					&.active
						.check-box
							&:before
								content: "\EB82"
								color: map-get($colors, main)

.product-filter-price
	.price-price-slider
		height: 5px
		border: 0 !important
		background: #bcbcbc
		margin: 0 5px 10px
		.ui-slider-handle
			border-radius: 50%
			top: 50%
			transform: translateY(-50%)
			width: 15px
			height: 15px
			border: 1px solid #666666
			background-color: #fff
		.ui-slider-range
			background-color: map-get($colors, main)
			border: 0
	.price-filter-text-wrap
		margin-bottom: 15px
		font-size: 14px
	.price-filter-input-wrap
		input
			width: 100%
			height: 30px
			background-color: #f0f0f0
			border: 1px solid #e5e5e5
			padding: 0 15px
			font-size: 14px
			color: #666
			margin-bottom: 10px
			border-radius: 4px
		.price-filter-input-btn
			display: inline-flex
			align-items: center
			justify-content: center
			width: r(100px)
			height: 30px
			border-radius: 4px
			border: 1px solid map-get($colors, main)
			color: map-get($colors, main)
			font-size: 14px

@media screen and ( max-width: 1024.9px )
	.filter-col
		padding-bottom: 0
	.product-filter-wrap
		display: none
		position: fixed
		z-index: 1000
		background: #fff
		top: 0
		bottom: 0
		left: 0
		width: 100%
		padding: 0
		overflow: auto
	.product-filter-container
		overflow: auto
		height: calc( 100% - 48px - 55px )
	.product-filter-heading
		height: 48px
		> *
			height: 100%
			padding: 0 15px
		.filter-close
			font-size: 30px
			position: relative
			top: 2px
		.filter-title
			font-size: 16px
			em
				margin-right: 5px
		.filter-reset
			font-size: 14px
	.product-filter-item
		padding: 20px 15px
		+ .product-filter-item
			margin-top: 0
			border-top: 5px solid #e5e5e5
		.product-filter-body
			display: block
			padding: 0
		.product-filter-title
			height: auto
			background-image: none
			background-color: transparent
			border-bottom: 0
			padding: 0
			margin-bottom: 20px
			font-size: 16px
			.arrow
				display: none
		.product-filter-group
			ul
				display: flex
				flex-wrap: wrap
				margin: 0 -5px
				li
					width: 50%
					border-bottom: 0
					margin-bottom: 10px
					padding: 0 5px
					.filter-link
						display: flex
						align-items: center
						justify-content: center
						height: 30px
						border: 1px solid #e5e5e5
						border-radius: 6px
						position: relative
						overflow: hidden
						.check-box
							display: none
						&.active
							border-color: map-get($colors, main)
							&:before
								content: url(../img/filter-active.png)
								top: 0
								right: 0
								position: absolute
		.filter-view-more
			height: 34px
			margin: 0 auto
			border: 2px solid map-get($colors, main)
			color: map-get($colors, main)
			width: 160px
			justify-content: center
			border-radius: 17px
			margin-top: 10px
	.product-filter-price
		.price-filter-input-wrap
			display: flex
			flex-wrap: wrap
			justify-content: space-between
			input
				width: calc(50% - 5px)
		.price-filter-input-btn
			width: 100%
	.product-filter-apply
		height: 55px
		display: flex
		align-items: center
		box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1)
		.filter-apply
			height: 40px
			width: 200px
			margin: 0 auto
			background: linear-gradient(90deg,#f05a94,#bc0049)
			color: #fff
			font-weight: 700
			border-radius: 20px