.promo-program-section
	margin: r(30px) 0

.promo-program-content
	border-radius: 5px
	overflow: hidden
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.06)
	border: 1px solid #e5e5e5
	background: #fff
	.promo-program-title
		background-image: url(../img/landing/coupon-title-bg.png) 
		background-repeat: no-repeat 
		background-position: 102% 15px
		background-color: map-get($colors, main)
		color: #fff
		height: r(50px)
		display: flex
		align-items: center
		justify-content: center
		text-transform: uppercase
		font-weight: 700
		font-size: r(24px)
	.promo-program-caption
		padding: r(30px)
		.promo-program-code
			height: r(225px)
			background: #f9e9f3
			display: flex
			align-items: center
			justify-content: center
			flex-direction: column
			padding: 0 15px
			border-radius: 5px
			word-break: break-all
			text-align: center
		.promo-program-text
			line-height: 1.375
			* + *
				margin-top: 10px
			ul
				li
					display: flex
					+ li
						margin-top: 5px
					&:before
						font-family: remixicon
						content: "\EB80"
						color: map-get($colors, main)
						margin-right: 5px
			a
				color: map-get($colors, cyan)
				text-decoration: underline
				text-underline-position: under

.landing-page-3
	.container
		> .row
			margin-left: -10px
			> *
				padding-left: 10px
	.nav-col
		width: 242px
		.nav
			box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.06)
			border-radius: 5px
			background: #fff
			line-height: 1.375
			ul
				text-align: right
				padding: r(30px) 0
				li
					+ li
						margin-top: r(20px)
					border-right: 5px solid transparent
					padding: 2.5px r(25px) 2.5px 0
					&.active
						border-right: 5px solid map-get($colors, main)
						a
							color: map-get($colors, main)
	.product-col
		flex: 1
		.landing-page-product-list
			border-radius: 5px
	@media screen and ( max-width: 1024.98px )
		.nav-col
			width: 100%
			.nav
				padding: 0 15px
				ul
					display: flex
					text-align: left
					overflow-x: auto
					li
						+ li
							margin-top: 0
							margin-left: 20px
						border-right: 0 !important
						padding: 0
						a
							white-space: nowrap