.home-4
	content-visibility: auto
	.tip-list-wrap
		background: #fff
		border-radius: 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

	.tip-list-heading
		padding: 10px
		border-bottom: 1px solid #eeeeee

	.view-more-desktop
		a
			min-height: 34px
			background: linear-gradient(90deg, rgba(240,90,148,1) 0%, rgba(188,0,73,1) 100%)
			color: #fff
			padding: 0 15px
			border-radius: 17px
	.tip-list
		padding: 10px

	.tip-item
		border-radius: 10px
		overflow: hidden
		display: block
		position: relative
		.tip-caption
			background-color: rgba(map-get($colors, blue), 0.85)
			transition: 0.3s all
			color: #fff
			height: 60px
			.title
				line-height: 1.375
				font-weight: 700
			.icon
				font-size: r(36px)
				margin-right: 10px
		&:hover
			.tip-caption
				background-color: rgba(map-get($colors, main), 0.85)
		&.tip-item-big
			.tip-img
				+img-scale(418 / 590 * 100%)
			.tip-caption
				padding: 0 15px
				.title
					+line(1)
					font-size: r(18px)
		&.tip-item-small
			.tip-img
				+img-scale(193 / 272 * 100%)
			.tip-caption
				padding: 0 10px
				.title
					font-size: r(14px)
					+line(2)
	@media screen and ( min-width: 1024.98px )
		.swiper-wrapper
			display: block
			margin-bottom: -20px
			&:after
				display: block
				content: ''
				clear: both
			.swiper-slide
				&:nth-child(1)
					float: left
					width: 50%
					padding-right: 10px
				&:nth-child(n + 2)
					width: 25%
					float: right
					padding-left: 20px
					margin-bottom: 20px
				&:nth-child(n + 4)
					margin-bottom: 0
	@media screen and ( max-width: 1024.98px )
		border-top: 6px solid #ced1d8
		padding-bottom: 0 !important
		.container
			padding: 0
			max-width: none
		.tip-list-wrap
			border-radius: 0
		.tip-item
			&.tip-item-big
				.tip-img
					padding-top: 193 / 272 * 100%
				.tip-caption
					.title
						-webkit-line-clamp: 2
						font-size: 14px
			&.tip-item-small
				.tip-caption
					.title
						font-size: 14px