.product-section
	padding: 30px 0 60px

.product-slider
	.swiper-slide
		height: auto
		.product-item
			height: 100%
	@media screen and ( max-width: 768.98px )
		padding: 0 5px
		.product-item
			border-right: 0
			background: #fff
			padding: 10px
			border: 1px solid #e5e5e5
			border-radius: 4px