.policy-content.article-content
	line-height: 1.5
	h2
		text-transform: uppercase
		color: map-get($colors, main)
		font-weight: 700
	h3
		font-size: r(14px)
		font-weight: 700
	> * + *
		margin-top: r(10px)
	> * + h2
		margin-top: r(35px)
	> * + h3
		margin-top: r(15px)
	ul
		list-style: disc
		padding-left: 1.25rem
		li
			+ li
				margin-top: 5px