.news-detail-wrap
	padding: r(30px)
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	background-color: #fff

.news-detail-meta
	padding-bottom: 15px
	border-bottom: 1px solid #ebebeb

.news-detail-date
	em
		position: relative
		top: 3px

.news-detail-toc
	padding: r(20px)
	background: rgba(map-get($colors, main), 0.1)
	ol
		list-style: decimal
		counter-reset: item
		line-height: 1.5
		li
			display: block
			position: relative
			color: map-get($colors, blue)
			ol
				padding-left: 1rem
			a
				&:hover
					text-decoration: underline
			&:before 
				content: counters(item, ".")". " 
				counter-increment: item

.news-detail-content
	h2
		font-size: r(22px)
		font-weight: 700
	h3
		font-size: r(18px)
		font-weight: 600
	h4
		font-weight: 600

.news-detail-middle-product-slider
	padding: r(30px) 0
	border-top: 1px solid #ebebeb
	border-bottom: 1px solid #ebebeb
	margin: 0 -30px
	.swiper-slide
		height: auto
	.swiper-btn
		&.swiper-btn-prev
			left: -10px
			right: auto
		&.swiper-btn-next
			left: auto
			right: -10px

.news-detail-product
	padding: 0 r(50px) 0 r(20px)
	border-right: 1px solid #ebebeb
	height: 100%
	.img
		width: 50%
		margin-right: r(20px)
	.caption
		flex: 1 1 0%
	.text
		ul
			list-style: disc
			padding-left: 1rem
	@media screen and ( max-width: 768.98px )
		flex-direction: column
		padding: 0 r(20px)
		.img
			width: auto
			margin-right: 0
			margin-bottom: r(20px)
			flex: r(147px) 0 0

.news-detail-promo-product
	margin: 0 r(-30px)
	.section-title
		padding: 0 r(30px)
		padding-bottom: 15px
		border-bottom: 1px solid #ebebeb

.news-detail-other
	margin-top: r(40px)
	.news-detail-other-heading
		height: r(40px)
	.other-title
		padding-bottom: 10px
		position: relative
		&:after
			content: ''
			position: absolute
			width: r(60px)
			height: 2px
			background: map-get($colors, blue)
			bottom: 0
			left: 0
	.swiper-slide
		height: auto
	.swiper-navigation
		display: flex
		margin: 0 -5px
		position: absolute
		top: 0
		right: 0
		.swiper-btn
			position: static
			transform: none
			background-color: #fff
			margin: 0 5px
	@media screen and ( max-width: 768.98px )
		margin-top: r(30px)
		.swiper-navigation
			position: static
			justify-content: center
			margin-top: 20px

.news-detail-lastest-news
	.lastest-title
		padding-bottom: 10px
		position: relative
		&:after
			content: ''
			position: absolute
			width: r(60px)
			height: 2px
			background: map-get($colors, blue)
			bottom: 0
			left: 0

	.lastest-news-item
		display: flex
		.img
			width: r(109px)
			a
				+img-scale(100%)
		.caption
			flex: 1 1 0%
			padding-left: 15px
			.date
				em
					position: relative
					top: 3px
			.title
				a
					+line(3)
					&:hover
						color: map-get($colors, main)
		+ .lastest-news-item
			margin-top: r(20px)
			padding-top: r(20px)
			border-top: 1px solid #d7d7d7

.news-detail-comment-section
	.commentpanel
		margin-top: r(10px)
	.product-review 
		.medium-rating,.start-overview
			display: none
		.btn-wrap
			width: auto
			border-left: 0
			margin-left: 0
			padding-left: 0
			text-align: left
			p
				display: none
			.btn-comment
				background-image: none
				background-color: map-get($colors, blue)
				margin-top: 0
	.rating-star
		display: none


.news-detail-page
	@media screen and ( max-width: 768.98px )
		main
			background: #fff
		.section
			padding-top: 0
			.container
				padding: 0

		.news-detail-wrap
			padding-left: 15px
			padding-right: 15px
			box-shadow: 
			border-bottom: 5px solid #e5e5e5

		.news-detail-promo-product,.news-detail-middle-product-slider
			margin-left: 0
			margin-right: 0

		.news-detail-promo-product 
			.section-title
				padding: 0 0 15px

		.news-detail-other,.news-detail-lastest-news
			padding: 0 15px
			
		.news-detail-comment-section
			margin-top: 0

		.news-detail-lastest-news
			padding-top: 20px
			border-top: 5px solid #e5e5e5
			.lastest-news-item
				border: 1px solid #eeeeee !important
				border-radius: 5px
				overflow: hidden
				+ .lastest-news-item 
					padding-top: 0
					border-top: 
				.img
					flex: 0 0 r(153px)
					a
						padding-top: 103 / 153 * 100%
				.caption
					background-color: #f9f9f9
					padding: 10px 15px
					.title
						font-size: 14px
				&:first-child
					flex-direction: column
					.img
						flex: 1 1 0%
						width: 100%

		.news-detail-other
			.swiper-navigation 
				.swiper-btn
					background-color: #f3f3f3

		.news-item
			.news-caption
				background-color: #f9f9f9