.view-more-mobile
	padding: 15px 0
	border-top: 1px solid #eee
	font-size: 14px

.swiper-btn
	position: absolute
	top: 50%
	transform: translateY(-50%)
	z-index: 2
	width: r(40px)
	height: r(40px)
	display: flex
	align-items: center
	justify-content: center
	color: #999
	font-size: r(18px)
	border-radius: 50%
	cursor: pointer
	background: #f3f3f3
	transition: 0.3s all
	&.swiper-button-disabled
		visibility: hidden
	&.swiper-btn-prev
		right: 100%
		transform: translateX(50%) translateY(-50%)
	&.swiper-btn-next
		left: 100%
		transform: translateX(-50%) translateY(-50%)
	&:hover
		background: linear-gradient(135deg,#bc0049 10%,#f05a94)
		color: #fff

.btn
	display: inline-flex
	align-items: center
	justify-content: center
	font-weight: 700
	&.btn-blue
		background-color: map-get($colors, blue)
		color: #fff
	&.btn-white
		background-color: map-get($colors, white)
	&.btn-gradient
		background: $gradient-1
		color: #fff