.product-deal-wrapper
	padding: 20px
	border: 2px solid map-get($colors, main)
	border-radius: 10px
	.swiper-slide
		height: auto

	.swiper-btn
		background: transparent !important
		font-size: r(36px)
		width: auto
		height: auto
		transform: translateY(-50%) !important
		&:hover
			color: map-get($colors, main)
		&.swiper-btn-prev
			left: 0
			right: auto
		&.swiper-btn-next
			right: 0
			left: auto

	.product-deal-heading
		padding-bottom: 10px
		border-bottom: 1px solid #eeeeee
		margin-bottom: r(15px)

	.product-deal-item
		display: flex
		flex-direction: column
		height: 100%
		.product-deal-item-price
			margin-bottom: r(20px)
			.price
				font-size: r(20px)
				font-weight: 700
			.old-price
				color: #999
				font-size: r(14px)
		
		.product-deal-item-vnpay
			height: 24px
			border-radius: 12px
			background-color: #f8f8f8
			font-size: r(14px)
			padding: 4px 10px
			display: flex
			align-items: center
			.vnpay-price
				color: map-get($colors, main)
				margin-left: 4px 

		.product-deal-item-name
			margin-top: 10px
			margin-bottom: 10px
			+line(2)

		.product-deal-item-image
			padding-top: 100%
			position: relative
			> img
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%,-50%)
				
		.product-deal-coupon
			position: absolute
			height: 24px
			width: 80px
			background-image: url(../img/coupon-bg.png)
			background-repeat: no-repeat
			background-size: contain
			color: #fff
			font-size: 12px
			font-weight: 700
			display: flex
			align-items: center
			padding-left: 15px
			text-transform: uppercase
			bottom: 0
			left: 0

		.product-deal-icon
			position: absolute
			top: 0
			right: 5px
			ul
				li
					+ li
						margin-top: 5px
					span
						height: r(40px)
						width: r(40px)
						display: flex
						align-items: center
						justify-content: center
						border-radius: 4px
						color: #fff
						text-transform: uppercase
						font-size: r(12px)
						line-height: 1.2
						text-align: center
						&.icon-brown
							background: linear-gradient(0deg, rgba(232,143,61,1) 0%, rgba(203,72,29,1) 100%)
						&.icon-blue
							background: linear-gradient(0deg, rgba(31,158,217,1) 0%, rgba(0,111,197,1) 100%)
							font-size: r(10px)
					img
						max-width: r(40px)

		.product-deal-progress
			margin-top: auto
			.deal-progress-bar-text
				font-size: 12px
				margin-bottom: 10px

			

	.product-deal-deadline
		margin-top: 20px
		.deal-deadline-text
			flex: 1
			padding-right: 30px
			strong
				font-size: 14px

		.deal-deadline-countdown
			width: 185px
			display: flex
			justify-content: space-between
			.count
				width: 25%
				span
					display: block
				.number
					width: 40px
					height: 40px
					display: flex
					align-items: center
					justify-content: center
					background-color: #eeeeee
					color: map-get($colors, blue)
					font-weight: 700
					margin: 0 auto
				.unit
					text-align: center
					font-size: 12px
					margin-top: 5px
					text-transform: uppercase

	.product-deal-pagination
		display: flex
		justify-content: center
		margin: r(15px) -5px 0
		padding-top: 15px
		border-top: 1px solid #eeeeee
		span
			margin: 0 5px
			display: block
			font-size: 0
			width: 18px
			height: 18px
			border-radius: 50%
			border: 2px solid #dddddd
			background-color: #fff
			opacity: 1
			&.swiper-pagination-bullet-active
				background-color: map-get($colors, main)
				border: 2px solid transparent

	@media screen and ( max-width: 1280.98px ) and (min-width: 1024px)
		.product-deal-item
			.product-deal-item-price
				.price
					font-size: r(18px)
					font-weight: 700
				.old-price
					color: #999
					font-size: r(12px)

	@media screen and ( max-width: 1024.98px )
		padding: 0 0 10px
		border: 0
		border-radius: 0
		display: flex
		flex-direction: column
		.product-deal-heading
			background: #fff
			padding: 15px
			border-bottom: 1px solid #eeeeee
			order: 1
			display: flex
			align-items: center
			justify-content: space-between
			font-weight: 700
			.view-more
				text-transform: uppercase
				font-size: 12px
		.product-deal-pagination
			display: none
		.product-deal-item
			background: #fff
			padding: 10px
			border: 1px solid #e5e5e5
			border-radius: 4px
			.product-deal-item-name
				order: 1
				margin-top: 0
			.product-deal-item-image
				order: 2
			.product-deal-item-top
				order: 3
				margin-top: 10px
			.product-deal-progress
				order: 4
			.product-deal-item-vnpay
				display: none
			.product-deal-item-price
				text-align: center
				width: 100%
				> *
					display: block
				.price
					font-size: 18px
				.old-price
					font-size: 12px
			.product-deal-coupon
				width: 66px
				height: 22px
				font-size: 10px
			.product-deal-progress
				.deal-progress-bar
					position: relative
					height: 14px
					background: #cccccc
					border-radius: 7px
				.deal-progress-bar-text
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%,-50%)
					font-size: 10px
					font-weight: 700
					color: #fff
			.product-deal-icon
				right: -5px
		.product-deal-slider
			padding: 0 5px
			order: 3
		.product-deal-deadline
			order: 2
			flex-direction: column
			background-image: url(../img/countdown.png)
			background-repeat: no-repeat
			background-size: contain
			background-color: map-get($colors, blue)
			background-position: right
			margin-top: 0
			margin: 0 15px 10px
			padding: 15px
			border-radius: 15px
			.deal-deadline-text
				color: #fff
				text-align: center
				margin-bottom: 15px
				padding-right: 0
			.deal-deadline-countdown
				width: 100%
				max-width: 215px
				margin: 0 auto
				.count
					padding: 0 10px
					+ .count
						border-left: 1px solid rgba(#fff , 0.5)
					.number
						color: #fff
						background: transparent
						height: auto
						width: auto
						font-size: 24px
						margin-bottom: 10px
					.unit
						color: #fff