.about-1
	.row
		border-radius: 10px
		overflow: hidden

	.content
		padding: r(30px)
		background-image: url(../img/about/bg-1.png)
		background-repeat: no-repeat
		background-position: 102% 106%
		height: 100%

	.text
		max-height: 325px
		overflow: auto
		padding-right: 15px
		&::-webkit-scrollbar
			width: 3px
			background: map-get($colors, blue)
		&::-webkit-scrollbar-thumb
			background: map-get($colors, main)

	.img
		height: 100%
		img
			width: 100%
			height: 100%
			object-fit: cover
	
	@media screen and ( max-width: 768.98px )
		padding: 0 !important
		.container
			max-width: none
			padding: 0
		.row
			border-radius: 0
			flex-direction: column-reverse
			padding: 15px
		.text
			max-height: none
			padding-right: 0
			font-size: r(20px)
		.content
			padding: 15px 0 0
			background-image: none
		.img
			border-radius: 10px
			overflow: hidden

.about-2
	background-image: url(../img/about/bg-2.png)
	background-repeat: no-repeat
	background-size: cover
	.item
		display: flex
		align-items: center
		padding: r(20px)
		border-radius: 5px
		.icon
			height: r(60px)
			width: r(60px)
			border-radius: 50%
			background-image: $gradient-1
			display: flex
			align-items: center
			justify-content: center
			padding: r(15px)
		.text
			flex: 1
			padding-left: 15px
	@media screen and ( max-width: 768.98px )
		.item
			flex-direction: column
			.icon
				margin-bottom: 10px
			.text
				padding-left: 0
				text-align: center
				font-size: r(20px)
		.row
			margin-bottom: -5px
			margin-left: -5px
			> *
				padding-left: 5px
				padding-bottom: 5px

.about-3
	.row
		border-radius: 10px
		overflow: hidden
	.content
		display: flex
		align-items: center
		padding: r(30px)
		height: 100%
	.btn
		height: 40px
		width: 160px
		border-radius: 20px
	@media screen and ( max-width: 768.98px )
		.row
			background: transparent
		.content
			padding: 0
			margin-top: 10px
			height: auto
			font-size: r(20px)

.about-4
	.slider-partner
		.img
			height: 105px
			border-radius: 10px
			border: 1px solid transparent
			transition: 0.3s all
			display: flex
			align-items: center
			justify-content: center
			img
				max-height: 40px
			&:hover
				background: #fff
				box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.05)
				border: 1px solid #eeeeee
		.swiper-btn
			margin: 0 30px
	@media screen and ( max-width: 1280px )
		.slider-partner
			.swiper-btn
				position: static
				transform: none
				margin: 0 5px
		.swiper-navigation
			display: flex
			align-items: center
			justify-content: center
	@media screen and ( max-width: 768.98px )
		.swiper-wrapper
			flex-wrap: wrap
			margin-left: -20px
			margin-bottom: -20px
			.swiper-slide
				width: 50%
				padding-left: 20px
				padding-bottom: 20px
		.swiper-navigation
			display: none