.product-detail-section
	padding: r(10px) 0 r(50px)

.product-detail-wrap
	background-color: #fff
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	+ .product-detail-wrap
		margin-top: r(30px)
	@media screen and ( min-width: 1024px )
		.border-right
			border-right: 1px solid #ebebeb

.product-detail-heading
	padding: 10px r(20px) 0
	border-bottom: 1px solid #eeeeee
	> *
		margin-bottom: 10px
.product-detail-like
	&.active
		em:before
			content: "\EE0E"
.product-detail-info-container
	padding: 0 0 r(30px) 0
	@media screen and ( max-width: 1024.98px )
		.col-12
			order: 2
		.col-lg-5
			order: 1
			border-bottom: 1px solid #ebebeb
		.col-lg-4
			order: 3
		.col-lg-3
			order: 4
		

.product-detail-images-wrap
	padding-top: 400 / 500 * 100%
	position: relative
	overflow: hidden
	.img
		display: block
	.swiper-container
		position: absolute
		top: 0
		left: 0
		width: 100%
		height: 100%
	.swiper-slide
		overflow: hidden
	.swiper-btn
		z-index: 20
		&.swiper-btn-prev
			right: auto
			left: 15px
			transform: translateY(-50%)
		&.swiper-btn-next
			right: 15px
			left: auto
			transform: translateY(-50%)
	.img
		position: relative
		height: 100%
		img
			position: absolute
			max-width: calc(100% - 100px)
			top: 50%
			left: 50%
			transform: translate(-50%,-50%)
	

.product-detail-thumbnail-wrap
	height: 64px
	margin-top: 20px
	padding: 0 r(20px)
	.img
		height: 64px
		display: flex
		align-items: center
		justify-content: center
		border: 1px solid #ebebeb
		overflow: hidden
		img
			max-width: 80%
	.swiper-slide-thumb-active
		.img
			border-color: map-get($colors, main)

.move-cursor
	font-size: 12px
	color: #999
	// position: absolute
	// bottom: 0
	// left: 50%
	// transform: translateX(-50%)
	// z-index: 2
	em
		font-size: r(24px)
		color: map-get($colors, blue)
		margin-right: 8px

.product-detail-video
	padding: 0 r(20px)
	margin-top: r(30px)
	ul
		display: flex
		flex-wrap: wrap
		margin-left: -5px
		margin-bottom: -5px
		li
			width: 20%
			padding-left: 5px
			margin-bottom: 5px
			a
				display: block
				+img-scale(66 / 96 * 100%)
				border-radius: 5px
				overflow: hidden
				background-color: #000
				transition: 0.3s all
				border: 1px solid #fff
				&:before
					position: absolute
					content: ''
					top: 0
					left: 0
					width: 100%
					height: 100%
					background-color: map-get($colors, main)
					transition: 0.3s all
					opacity: 0
				img
					opacity: 0.85 !important
				em
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%,-50%)
					z-index: 3
					font-size: r(36px)
					color: #fff
				&:hover
					border: 1px solid map-get($colors, main)
					&:before
						opacity: 0.9

.product-detail-icon
	position: absolute
	top: 0
	left: 15px
	z-index: 3
	ul
		li
			+ li
				margin-top: 5px
			span
				height: 40px
				width: 40px
				display: flex
				align-items: center
				justify-content: center
				border-radius: 4px
				color: #fff
				text-transform: uppercase
				font-size: 12px
				line-height: 1.2
				text-align: center
				&.icon-brown
					background: linear-gradient(0deg, rgba(232,143,61,1) 0%, rgba(203,72,29,1) 100%)
				&.icon-blue
					background: linear-gradient(0deg, rgba(31,158,217,1) 0%, rgba(0,111,197,1) 100%)
					font-size: 10px
			img
				max-width: 40px

.product-detail-info-wrap
	@media screen and ( max-width: 1024.98px )
		padding: 10px 15px

.rating-star
	width: 67px
	height: 13px
	background-image: url(../img/star-group.png)
	background-repeat: no-repeat
	div
		background-image: url(../img/star-group-on.png)
		background-repeat: no-repeat
		height: 100%
.product-detail-rating

	.rating-number
		color: #007ff0
		margin-left: 10px

.product-detail-brand
	> *
		margin-bottom: 8px
		display: flex
		span
			&:first-child
				width: 90px
	@media screen and ( max-width: 768.98px )
		font-size: r(18px) !important

.product-detail-code
	@media screen and ( max-width: 768.98px )
		font-size: r(18px) !important

.product-detail-divider
	border-top: 1px solid #ebebeb
	margin: r(20px) 0
	@media screen and ( max-width: 576px )
		margin: 10px -15px

.product-detail-brief
	ul
		list-style: disc
		padding-left: 1rem
		li
			+ li
				margin-top: 5px
	@media screen and ( max-width: 768.98px )
		font-size: r(18px) !important

.product-detail-price
	@media screen and ( max-width: 768.98px )
		.fz-14
			font-size: r(18px) !important

.deal-deadline-text
	@media screen and ( max-width: 768.98px )
		font-size: r(18px) !important

.product-detail-promo
	margin-top: r(20px)
	padding: r(20px)
	background-color: #f9f9f9
	border-top: 1px solid #ebebeb
	ul
		li
			display: flex
			img
				max-width: 60px
				margin-right: r(20px)
			a
				display: flex
			+ li
				margin-top: 5px
			&:before
				content: "\EB80"
				font-family: remixicon
				color: map-get($colors, main)
				margin-right: 5px
				position: relative
				top: 1.5px

.spin-input-wrap
	display: inline-flex
	height: r(40px)
	border-radius: 20px
	overflow: hidden
	border: 1px solid #ebebeb
	.spin-btn
		width: r(40px)
		display: flex
		align-items: center
		justify-content: center
		background-color: #eeeeee
		cursor: pointer
		color: #666
	input
		width: r(60px)
		text-align: center
		border: 1px solid #ebebeb
		border-top: 0
		border-bottom: 0
		font-size: 16px

.product-detail-buy-now
	width: r(142px)
	.btn
		height: r(40px)
		width: 100%
		border-radius: r(20px)

.btn-sold-out-letter
	height: r(40px)
	width: 100%
	border-radius: r(20px)
	background: map-get($colors, blue)
	color: #fff

.product-detail-addtocart
	flex: 1
	.btn
		height: r(40px)
		width: 100%
		border-radius: r(20px)
		padding: 0 8px

	.btn-find-store
		background-color: #f2f7fc
		color: map-get($colors, blue)
		font-size: r(13px)
		border: 1px solid #ebebeb
		span
			margin-right: 4px

	.btn-addtocart
		background-image: linear-gradient(90deg,#f05a94,#bc0049)
		color: #fff
		font-size: r(14px)
		img
			margin-right: 8px
			display: block
			max-width: r(23px)

.product-detail-frequent
	.plus
		margin: 0px r(10px)
		color: #999999
		font-size: 20px
		display: block
	.image-list
		flex-wrap: wrap
		margin-bottom: -10px
	.item
		flex: 0 0 r(90px)
		height: r(90px)
		padding: 4px
		border: 1px solid #ebebeb
		border-radius: 4px
		margin-bottom: 10px
		img
			object-fit: contain
			width: 100%
			height: 100%
	.frequently-product-item
		display: flex
		align-items: center
		font-size: r(14px)
		color: #999999
		+ .frequently-product-item
			margin-top: 5px
		.icon
			position: relative
			top: 1px
			&:before
				font-family: remixicon
				content: "\EB7F"
		.frequently-product-name
			flex: 1 1 0%
			display: flex
			align-items: center
			margin: 0 10px 0 5px
			white-space: nowrap
			overflow: hidden
			line-height: 1.2
			span
				text-overflow: ellipsis
				overflow: hidden
		&.active
			color: #666
			.icon
				&:before
					content: "\EB82"
					color: map-get($colors, main)
	.frequently-button
		height: 30px
		min-width: r(230px)
		background-image: linear-gradient(90deg,#f05a94,#bc0049)
		font-weight: 400
		color: #fff
		font-size: 14px
		border-radius: 5px
		padding: 0 8px
		span
			margin: 0 2px
	@media screen and ( max-width: 576px )
		.frequently-button
			height: 40px
			width: 100%
			border-radius: 20px

.product-detail-aside
	@media screen and ( min-width: 1024px )
		padding-right: r(30px)

.product-detail-specification
	border-radius: 5px
	border: 1px solid #ebebeb
	box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.1)
	padding: 15px 10px
	ul
		li
			display: flex
			align-items: center
			+ li
				margin-top: r(15px)
				padding-top: r(15px)
				border-top: 2px dotted #ebebeb
			.icon
				width: 60px
				padding-left: 10px
			.text
				flex: 1 1 0%
				font-size: r(13px)
				line-height: 1.375
				a
					color: #007ff0
					text-decoration: underline

.product-detail-banner
	img
		width: 100%

.product-detail-article,.product-detail-data,.product-detail-comment
	padding: r(20px)
	.section-title
		margin-bottom: r(20px)

.product-detail-article
	.show-hide-content
		position: relative
		&.hide
			max-height: r(750px)
			overflow: hidden
			&:before
				position: absolute
				content: ''
				height: 150px
				bottom: 0
				left: 0
				width: 100%
				background: linear-gradient(0deg, rgba(255,255,255,1) 20%, rgba(255,255,255,0) 100%)
		.btn-show-content
			position: absolute
			bottom: 0
			left: 50%
			transform: translateX(-50%)
			color: map-get($colors, blue)
			display: flex
			align-items: center
			justify-content: center
			cursor: pointer
			em
				margin-left: 10px
			&.active
				position: static
				transform: none

.product-detail-data
	.data-table
		table
			width: 100%
			font-size: r(14px)
			line-height: 1.375
			tr
				td
					padding: r(15px) r(30px)
					border: 1px solid #ebebeb
					&:first-child
						font-weight: 700
						background: #f0f0f0
						width: 30%
					a
						color: #007ff0
		ul
			list-style: disc
			padding-left: 1rem
			line-height: 1.375
			li
				+ li
					margin-top: 5px
	@media screen and ( max-width: 768.98px )
		.data-table
			table
				font-size: r(18px)
				tr
					td
						&:first-child
							width: 40%

.product-detail-promo-product
	background-color: #fff
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	.section-title
		padding: 20px
		font-size: r(20px)
	.product-item
		border-bottom: 1px solid #ebebeb

.product-detail-viewed
	margin-top: r(30px)

.product-detail-page
	@media screen and ( max-width: 1024.98px )
		.breadcrumb-wrapper
			display: none
		.product-detail-section
			padding-top: 0px
			// background: #fff
			.row
				> *
					padding-bottom: 0
		.product-detail-wrap
			border-radius: 0
			box-shadow: none
		.product-detail-like
			margin-left: auto
		.swiper-slide-count
			width: 50px
			height: 20px
			position: absolute
			z-index: 4
			border-radius: 20px
			display: flex
			align-items: center
			justify-content: center
			font-size: 14px
			left: auto
			right: 15px
		.product-detail-fixed-nav
			position: fixed
			z-index: 99
			bottom: 0
			left: 0
			width: 100%
			background: #fff
			box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1)
		.product-detail-nav
			padding: 7.5px 10px
			margin: 0 -5px
			display: flex
			align-items: center
			height: 55px
			a
				display: inline-flex
				align-items: center
				justify-content: center
				height: 40px
				border-radius: 20px
				background: #f2f7fc
				padding: 0 15px
				color: map-get($colors, blue)
				border: 1px solid #ebebeb
				margin: 0 5px
				em
					font-size: 24px
				span
					margin-left: 5px
					font-size: 13px
				&.find-store
					flex-grow: 1
				&.hotline,&.chat
					width: 40px
				&.choose-buy
					background: linear-gradient(90deg,#f05a94,#bc0049)
					color: #fff
					font-size: 14px
					font-weight: 700
					flex-grow: 1
		.button-group
			display: flex
			align-items: center
			margin: 0 -5px
			padding: 7.5px 10px
			.btn
				height: 40px
				width: 50%
				margin: 0 5px
				border-radius: 20px
				&.add-to-cart
					background: linear-gradient(90deg,#f05a94,#bc0049)
					color: #fff
		.product-to-buy
			padding: 25px 10px
			background: #f8f8f8
			.img
				width: 80px
				height: 80px
				border-radius: 5px
				border: 1px solid map-get($colors, main)
				overflow: hidden
				display: flex
				align-items: center
				justify-content: center
				margin-right: 10px
			.caption
				flex-grow: 1
				line-height: 1.375
				.name 
					font-size: 14px
				.price
					font-size: 16px
				.vnpay-price
					font-size: 10px
	@media screen and ( max-width: 576px )
		.product-detail-section
			.container
				padding: 0
		.product-detail-promo
			margin-left: -15px
			margin-right: -15px
		.product-detail-wrap 
			+ .product-detail-wrap 
				margin-top: 0
				border-top: 6px solid #ced1d8
		.product-detail-promo-product
			border-top: 6px solid #ced1d8
			border-radius: 0
		.product-detail-data 
			border-top: 6px solid #ced1d8
		.product-detail-viewed
			border-top: 6px solid #ced1d8
			background: #f4f4f4
			.product-detail-heading
				margin-bottom: 20px
		.product-detail-related
			background: #f4f4f4
			padding-bottom: 2rem
			.product-detail-heading
				margin-bottom: 20px
		.product-detail-aside
			padding: r(20px) 15px
			margin-top: r(10px)
			border-top: 1px solid #ebebeb
		.product-detail-promo-product 
			.section-title
				font-size: 1.5rem

#product-sold-out-letter
	line-height: 1.375
	width: 480px
	h3
		font-size: 1.5rem
		font-weight: 700
		margin-bottom: 1rem
	.form-group
		+ .form-group
			margin-top: r(30px)
		label
			display: inline-block
			font-weight: 500
		input[type="text"]
			border: 0
			border-bottom: 1px solid #ebebeb
			height: 35px
			padding: 5px 12px
			font-size: 14px
			width: 100%
			&::placeholder
				font-style: italic
				color: map-get($colors, light-grey)
	.form-btn
		margin-top: r(30px)
		input[type=submit]
			width: r(160px)
			height: r(40px)
			border-radius: r(20px)
			background: $gradient-1
			color: #fff
			font-weight: 700
			border: 0
			cursor: pointer