.product-category-list-wrap
	content-visibility: auto
	+ .product-category-list-wrap
		margin-top: r(50px)
	.product-category-list
		background: #fff
		border-radius: 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	.product-category-list-heading
		padding: 10px
		border-bottom: 1px solid #eeeeee
	.category-title
		.icon
			width: 40px
			height: 40px
			border-radius: 6px
			border: 1px solid #e5e5e5
			img
				filter: invert(16%) sepia(69%) saturate(1251%) hue-rotate(201deg) brightness(98%) contrast(94%)
	.category-tab
		ul
			li
				a
					min-height: 34px
					color: map-get($colors, blue)
					padding: 0 15px
					border-radius: 17px
				&.active
					a
						background: linear-gradient(90deg, rgba(240,90,148,1) 0%, rgba(188,0,73,1) 100%)
						color: #fff
	.product-category-tab
		display: none
		&.show
			display: block
	.product-category-list-body
		.row
			margin-left: 0
			margin-bottom: 0
			> *
				padding-left: 0
	@media screen and ( max-width: 1024.98px )
		border-top: 6px solid #ced1d8
		+ .product-category-list-wrap
			margin-top: 0
		.container
			padding: 0
			max-width: none
		.product-category-list
			border-radius: 0
			.product-item
				border-top: 1px solid #eee
		.product-category-list-body
			.row
				margin-left: 0
				margin-bottom: 0
				> *
					padding-left: 0
					padding-bottom: 0