.cart-login
	width: r(500px)
	max-width: 100%
	background: #fff
	border-radius: 10px
	box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.05)
	margin: 0 auto
	.login-heading 
		padding: r(20px) r(25px)
		border-bottom: 1px solid #ebebeb
		font-size: r(18px)
		color: map-get($colors, blue)
		text-transform: uppercase
		font-weight: 700
	.login-form
		padding: r(25px)
		.select-group
			ul
				li, li > span
					display: flex
					align-items: center
					input[type="radio"]
						appearance: none
						display: block
						width: 16px
						height: 16px
						background-image: url(../img/checkout-radio.png)
						background-repeat: no-repeat
						&:checked
							background-image: url(../img/checkout-radio-checked.png)
					label
						margin-left: 15px
					+ li
						margin-top: 10px

		.module-title
			text-transform: uppercase
			margin-top: r(30px)
			margin-bottom: r(15px)

		.form-group
			+ .form-group
				margin-top: 25px
			label
				display: block
				margin-bottom: 10px
			input[type="text"],input[type="password"]
				width: 100%
				height: 50px
				border-radius: 5px
				border: 1px solid #e1e1e1
				background-color: #fafafa
				padding: 0 20px
				&::placeholder
					color: #999999
			&.form-link
				display: flex
				justify-content: space-between
			.register-link,#ctl00_mainContent_ctl00_LoginCtrl_lnkPasswordRecovery
				font-size: 14px
			#ctl00_mainContent_ctl00_LoginCtrl_lnkRegisterExtraLink
				color: map-get($colors, cyan)
				text-decoration: underline

		input[type="submit"]
			height: r(40px)
			width: 100%
			background: #fff
			border-radius: r(20px)
			border: 2px solid map-get($colors, main)
			color: map-get($colors, main)
			font-weight: 700
			margin-top: 20px
			cursor: pointer

	.social-login
		padding: 0 r(25px) r(25px)
		a
			display: flex
			height: 40px
			border-radius: 20px
			color: #fff
			+ a
				margin-top: 20px
			em
				margin-right: 10px
				font-size: r(18px)
			&.facebook-login
				background: #4267B2
			&.google-login
				background: #df4a32

