.product-promo-wrapper
	padding: 22px 0 0
	.product-promo-heading
		padding-bottom: 10px
		border-bottom: 1px solid #eeeeee
		margin-bottom: 20px
	.swiper-slide
		height: auto
		padding-bottom: 20px
	.product-item
		height: 100%
		padding-bottom: 5px
	@media screen and ( max-width: 1024.98px )
		padding-top: 10px
		border-top: 6px solid #ced1d8
		.product-promo-heading
			padding: 0 15px 10px
			display: flex
			align-items: center
			justify-content: space-between
			font-weight: 700
			margin-bottom: 10px
			.view-more
				text-transform: uppercase
				font-size: 12px
		.product-list
			padding: 0 5px
		.product-item
			border-right: 0
			background: #fff
			padding: 10px
			border: 1px solid #e5e5e5
			border-radius: 4px
