.product-item
	display: flex
	flex-direction: column
	padding: 15px
	border-right: 1px solid #eee
	border-left: 1px solid transparent
	border-bottom: 1px solid transparent
	transition: 0.3s all
	position: relative
	overflow: hidden
	height: 100%
	&:hover
		.btn-buy
			transform: none

	.product-img
		padding-top: 100%
		position: relative
		background: #fff
		display: block
		> img
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%,-50%)
			max-height: 100%

	.product-name
		font-size: 14px
		margin-top: 10px
		flex: 1 1 0%
		a
			+line(2)
		&:hover
			color: map-get($colors, main)

	.product-price
		margin-top: 10px
		.price
			font-size: r(18px)
			font-weight: 700
			white-space: nowrap
		.old-price
			color: #999
			font-size: r(14px)
			margin-left: 10px
			white-space: nowrap

	.product-vnpay-price
		margin-top: 8px
		font-size: r(14px)

	.product-deal-coupon
		position: absolute
		height: 24px
		width: 80px
		background-image: url(../img/coupon-bg.png)
		background-repeat: no-repeat
		color: #fff
		font-size: 12px
		font-weight: 700
		display: flex
		align-items: center
		padding-left: 15px
		text-transform: uppercase
		bottom: 0
		left: 0

	.btn-buy
		position: absolute
		bottom: 10px
		right: 0
		z-index: 3
		padding: 6px 5px
		border-radius: 5px 0px 0px 5px
		transition: 0.3s all
		transform: translateX(100%)
		height: 40px
		width: 40px
		color: #fff
		display: flex
		align-items: center
		justify-content: center
		text-align: center
		font-size: 12px
		line-height: 1.375
		img
			max-width: 30px

	.product-icon
		position: absolute
		top: 0
		right: -5px
		ul
			li
				+ li
					margin-top: 5px
				span
					height: r(40px)
					width: r(40px)
					display: flex
					align-items: center
					justify-content: center
					border-radius: 4px
					color: #fff
					text-transform: uppercase
					font-size: r(12px)
					line-height: 1.2
					text-align: center
					&.icon-brown
						background: linear-gradient(0deg, rgba(232,143,61,1) 0%, rgba(203,72,29,1) 100%)
					&.icon-blue
						background: linear-gradient(0deg, rgba(31,158,217,1) 0%, rgba(0,111,197,1) 100%)
						font-size: r(10px)
				img
					max-width: r(40px)

	&:hover
		box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.05)
		border-left: 1px solid #eee
		border-bottom: 1px solid #eee
	@media screen and ( max-width: 1024.98px )
		border-bottom: 1px solid #eee