.mobile-product-category
	.product-category-link
		.text
			flex-direction: column
			width: 100%
			display: flex
			.icon
				width: 100%
				+img-scale(100%)
				border-radius: 25px
				overflow: hidden
			span
				display: block
				font-size: 14px
				margin-top: 10px
				text-align: center
				line-height: 1.3