.coupon-item
	border-radius: 5px
	overflow: hidden
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.06)
	border: 1px solid #e5e5e5
	background: #fff

	.coupon-title 
		background-image: url(../img/landing/coupon-title-bg.png) 
		background-repeat: no-repeat 
		background-position: 102% 15px
		background-color: map-get($colors, main)
		color: #fff
		height: r(50px)
		display: flex
		align-items: center
		justify-content: center
		text-transform: uppercase
		font-weight: 700
		font-size: r(24px)

	.coupon-caption
		padding: r(15px) r(25px)
		line-height: 1.375
		* + *
			margin-top: r(10px)
		p
			a
				color: map-get($colors, cyan)
				text-decoration: underline
				text-underline-position: under
	.coupon-deadline
		padding: r(15px) r(25px)
		border-top: 1px dashed #e5e5e5
		font-size: r(13px)