.faq-list
	.collapse-trigger
		display: flex
		align-items: center
		justify-content: space-between
		&.active
			color: map-get($colors, main)
			strong
				display: block
				padding-right: 20px
			em:before
				content: "\EA78"
	.collapse-target
		padding: r(20px) 0 0
		display: none
	.faq-item
		+ .faq-item
			margin-top: r(20px)
			padding-top: r(20px)
			border-top: 1px solid #e5e5e5
.support-faq
	@media screen and ( max-width: 768.98px )
		padding-bottom: 30px