.modulepager
	padding-bottom: r(30px)
	background: #fff
	.pagination
		display: flex
		list-style-type: none
		justify-content: center
		margin-top: r(30px)
		li
			margin: 0 5px
			&:last-child
				margin-right: 0
			a, span
				width: r(34px)
				height: r(34px)
				background: #fff
				border-radius: 4px
				display: flex
				justify-content: center
				align-items: center
				border: 1px solid #ebebeb
			// .LastPage,.FirstPage
			// 	display: none!important
			.NextPage
				font-size: 0
				border: 0
				background: transparent
				&:before
					font-family: "Material Design Icons"
					content: "\f142"
					color: map-get($colors, blue)
					font-size: r(20px)
			.BackPage
				font-size: 0
				border: 0
				background: transparent
				&:before
					font-family: "Material Design Icons"
					content: "\f141"
					color: map-get($colors, blue)
					font-size: r(20px)
			.FirstPage
				font-size: 0
				border: 0
				background: transparent
				&:before
					font-family: "Material Design Icons"
					content: "\f13d"
					color: map-get($colors, blue)
					font-size: r(20px)
			.LastPage
				font-size: 0
				border: 0
				background: transparent
				&:before
					font-family: "Material Design Icons"
					content: "\f13e"
					color: map-get($colors, blue)
					font-size: r(20px)

			&.active, &:hover
				a, span
					color: map-get($colors, main)
					border: 1px solid map-get($colors, main)
		

.newspager
	.modulepager
		background: transparent