.landing-page-1

	.landing-page-1-item
		border-radius: 10px
		padding: 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
		background: #ffffff
		+ .landing-page-1-item
			margin-top: r(50px)
	.landing-page-1-banner
		a
			+img-scale(500 / 1210 * 100%)
	.product-list
		margin-top: r(30px)
	.view-more-wrap
		padding: r(25px) 0
		text-align: center
		.btn
			height: 34px
			border-radius: 20px
			width: 130px
	@media screen and ( max-width: 768.98px )
		padding: 0 !important
		.container
			max-width: none
			padding: 0
		.landing-page-1-item
			border-radius: 0
			box-shadow: none
			padding: 15px
			+ .landing-page-1-item
				margin-top: 0
				border-top: 5px solid #e5e5e5
