.coupon-max-width
	padding: r(30px) 0
	.coupon-col
		width: 286px
	.coupon-item
		.coupon-caption
			padding: 15px
		.coupon-deadline
			padding: 15px
	@media screen and ( max-width: 576px )
		.coupon-col
			width: 100%

.landing-flashsale-hot
	@media screen and ( max-width: 768.98px )
		.product-list-wrap
			border-radius: 0
		.container
			padding: 0
			max-width: none
		.landing-page-product-list
			margin-bottom: 0
			border-bottom: 5px solid #e5e5e5
.landing-flashsale-list
	.landing-page-product-list
		box-shadow: none
		border-radius: 0
		.product-list
			border-top: 0
			position: relative
			&:before
				content: ''
				position: absolute
				bottom: 0
				height: 1px
				background: #fff
				left: 0
				width: 100%
				z-index: 2
			&:after
				content: ''
				position: absolute
				top: 0
				width: 1px
				background: #fff
				right: 0
				bottom: 0
				z-index: 2
		> .row
			margin-left: -10px
			> *
				padding-left: 10px
		.banner-col
			width: 380px
		.product-col
			flex: 1
		+ .landing-page-product-list
			padding-top: r(30px)
			border-top: 1px solid #e5e5e5
			margin-top: r(40px)
	@media screen and ( max-width: 1024.98px )
		.landing-page-product-list
			.banner-col
				display: none
	@media screen and ( max-width: 768.98px )
		padding: r(30px) 0 0 !important
		.container
			padding: 0
			max-width: none
		.landing-page-product-list
			+ .landing-page-product-list
				border-top: 5px solid #e5e5e5