.news-subnav-section
	background-color: #fff
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	height: 50px
	overflow: hidden
	@media screen and ( min-width: 1024.98px )
		.swiper-wrapper
			justify-content: center
			.swiper-slide
				width: auto
				.subnav-link
					display: flex
					align-items: center
					min-height: 50px
					font-size: r(18px)
					font-weight: 700
					color: map-get($colors, blue)
					padding: 0 20px
					&.active
						background: $gradient-1
						color: #fff
	@media screen and ( max-width: 1024.98px )
		background-color: rgba(map-get($colors, main), 0.75)
		height: 40px
		.news-subnav-wrap
			display: flex
			align-items: center
		.news-subnav
			margin-right: 15px
			width: calc(100% - 70px)
		.news-subnav-control
			margin: 0 -2.5px
			width: 70px
			.btn-slide
				width: 30px
				height: 30px
				background: #fb98be
				color: #fff
				font-size: 20px
				border-radius: 5px
				margin: 0 2.5px
				cursor: pointer
		.subnav-link
			height: 40px
			display: flex
			align-items: center
			color: #fff
			padding-left: 10px
			&.active
				background: #eea9c8