.search-result-wrap
	border-radius: 10px
	background-color: rgb(255, 255, 255)
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	+ .search-result-wrap
		margin-top: 30px

.product-result
	.search-top
		padding: r(20px) r(25px)
		border-bottom: 1px solid #ebebeb
		line-height: 1.375
		.section-title
			padding-bottom: 0
			padding-left: 0
	.searchresultsummary
		margin-top: 5px
		color: map-get($colors, blue)
		font-size: r(18px)
		span
			color: map-get($colors, main)
	@media screen and ( max-width: 1024.98px )
		.searchresultsummary
			padding-bottom: 15px
		.row
			margin: 0
			> *
				padding: 0
		.product-list-util
			justify-content: space-between
			margin-top: 15px

.searchresults
	.section-title
		padding: r(20px)
	.product-list
		border-top: 1px solid #eee
		.row
			margin: 0
			> *
				padding: 0
		.product-item
			border-bottom: 1px solid #eee
	.news-list
		padding: 0 r(20px) r(25px)
		.news-item
			border: 1px solid #eeeeee
			box-shadow: none
			.news-caption
				background-color: #f8f8f8
	.view-more-wrap
		padding: r(25px) 0
		text-align: center
		.btn
			height: 34px
			border-radius: 17px
			width: 150px
			em
				margin-left: 8px
			&.btn-white
				border: 1px solid map-get($colors, main)
				color: map-get($colors, main)
.search-filter
	@media screen and ( min-width: 1024px )
		padding: r(20px) r(25px)
		.product-filter-container
			display: flex
			flex-wrap: wrap
			background: #f5f5f5
			border-radius: 5px
		.product-filter-item
			box-shadow: none
			border: 0
			position: relative
			flex: auto
			max-width: 1/8*100%
			+ .product-filter-item
				margin-top: 0
				margin-left: 15px
			.product-filter-title
				background-image: none
				border-bottom: 0
				background-color: transparent
				white-space: nowrap
				.text
					font-weight: 400
					margin-right: 15px
			.product-filter-body
				position: absolute
				top: 100%
				left: 15px
				min-width: 250px
				background: #f5f5f5
				border-radius: 5px
				border: 1px solid #e5e5e5
				z-index: 5
			.product-filter-group 
				ul 
					li
						border-bottom: 0
						.filter-link 
							.text
								text-transform: none
			&:not(.product-filter-price)
				.product-filter-body
					padding: 15px 5px 15px 15px
				.product-filter-group
					overflow: auto
					max-height: 250px
					padding-right: 7px
					&::-webkit-scrollbar
						background: #e1e1e1
						width: 5px
						border-radius: 5px
					&::-webkit-scrollbar-thumb
						background: map-get($colors, blue)
						border-radius: 5px
.search-page
	@media screen and ( max-width: 768.98px )
		padding: 0 !important
		.search-result-wrap
			border-radius: 0
			+ .search-result-wrap
				margin-top: 0
				border-top: 5px solid #e5e5e5
		.container
			padding: 0
			max-width: none
		.search-top
			padding: 20px 0 0
			.search-title
				padding: 0 15px
			.searchresultsummary
				padding: 0 15px 15px
		.searchresults 
			.section-title
				padding: 15px
			.news-list
				> .row
					margin-bottom: -10px
					margin-left: -10px
					> *
						padding-bottom: 10px
						padding-left: 10px
						&:first-child
							flex: 0 0 100%
							max-width: 100%
		.product-list-util
			background-color: #f8f8f8
			background-image: url(../img/product/filter-bg.png)
			background-position: 105% 15px
			background-repeat: no-repeat
			padding: 15px
			border-top: 1px solid #e5e5e5
