.product-subnav-wrap
	padding-top: 10px
	padding-bottom: r(25px)
	border-bottom: 1px solid #e1e1e1
	.swiper-btn
		top: calc( 114px / 2 )
	.product-subnav-item
		display: block
		.img
			+img-scale(100%)
			border-radius: 15px
			overflow: hidden
		.text 
			text-align: center
			margin-top: r(15px)
			line-height: 1.2
	@media screen and ( min-width: 1280px )
		height: 192px
		overflow: hidden
	@media screen and ( max-width: 1280.98px )
		.swiper-btn
			top: calc( 87px / 2 )
	@media screen and ( max-width: 1024.98px )
		.swiper-btn
			top: calc( 111.66px / 2 )
	@media screen and ( max-width: 768.98px )
		.swiper-btn
			top: calc( 98px / 2 )
	@media screen and ( max-width: 576.98px )
		background-color: #fff
		.swiper-btn
			top: 50%
			display: none