.promo-section
	.promo-list-wrap
		background: #fff
		border-radius: 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	.promo-list-heading
		padding: 10px
		border-bottom: 1px solid #eeeeee
	.promo-title
		.icon
			width: 40px
			height: 40px
			border-radius: 6px
			border: 1px solid #e5e5e5
	.promo-list-body
		padding: 15px 10px 30px

	.promo-list
		.promo-col
			&:first-child
				flex: 0 0 100%
				max-width: 100%
				.promo-item
					.promo-img
						+img-scale(450 / 1210 * 100%)
					.promo-title
						font-size: r(24px)
					.promo-desc 
						font-size: 1rem
		.promo-item
			display: block
			.promo-img
				+img-scale(370 / 590 * 100%)
				border-radius: 10px
				overflow: hidden
			.promo-caption
				line-height: 1.375
				padding: 20px 10px 0
				background: #fff
			.promo-title
				text-transform: uppercase
				color: map-get($colors, blue)
				margin-bottom: 5px
				font-size: r(18px)
				font-weight: 700
			.promo-desc 
				font-size: r(14px)

	.product-list-wrap
		margin-top: 30px
		.product-list
			.row
				margin: 0
				> *
					padding: 0
			.product-item
				border-bottom: 1px solid #eee

	.view-more-wrap
		padding: r(25px) 0
		text-align: center
		.btn
			height: 40px
			border-radius: 20px
			width: 220px
			&.btn-white
				border: 1px solid map-get($colors, main)
				color: map-get($colors, main)

	@media screen and ( max-width: 768.98px )
		padding: 25px 0 !important
		.container
			max-width: none
			padding: 0
		.promo-list-wrap
			background: transparent
			box-shadow: none
			padding: 0
		.promo-list-heading
			padding: 0
			border-bottom: 0
		.promo-list-body
			padding: 15px 15px 0
		.promo-title
			.icon
				display: none
		.promo-list
			.promo-col
				&:first-child
					.promo-item
						.promo-img
							padding-top: 370 / 590 * 100%
			.promo-item
				border-radius: 10px
				overflow: hidden
				.promo-img
					border-radius: 0
				.promo-caption
					padding: 20px
				.promo-title
					font-size: 16px !important
				.promo-desc
					font-size: 14px !important
		.product-list-wrap
			border-radius: 0
			border-top: 5px solid #e5e5e5