footer
	background-color: #fff
	@media screen and ( max-width: 1024.9px )
		padding-bottom: 60px
.footer-subscription
	padding: 26px 0
	background-position: center
	color: #fff
	.subscription-title
		.icon
			font-size: r(48px)
			height: r(48px)
		.text
			line-height: 1.3
			padding-left: 15px
			h3
				text-transform: uppercase
				font-size: r(18px)

	.subscribefrm
		display: flex
		flex-wrap: wrap
		.fa-exclamation-triangle
			width: 100%
			color: #fff
		input
			height: 40px
			border: 0
			background: #fff
			padding: 0 15px
			font-size: 14px
			flex: 1 1 0%
			&::placeholder
				color: #999
				font-style: italic
		button
			width: r(180px)
			height: 40px
			background-color: map-get($colors, blue)
			color: #fff
			display: flex
			align-items: center
			justify-content: center
			font-weight: 700
			text-transform: uppercase
			border: 0

.footer-middle
	border-bottom: 1px solid #e5e5e5
	@media screen and ( max-width: 768.98px )
		.row
			margin: 0 0 r(-15px) r(-20px)
			justify-content: center
			> *
				padding: 0 0 r(15px) r(20px)
	
.footer-download-app
	padding: 20px 0
	h3
		text-transform: uppercase
		font-weight: 700
	p
		font-size: r(14px)
		color: #666
	
	.app-link
		display: flex
		a
			display: inline-block
			+ a
				padding-left: r(20px)
			

	.app-qr
		text-align: right
		strong
			margin-right: r(20px)

	@media screen and ( max-width: 576px )
		.app-qr
			text-align: left
		.app-link
			justify-content: center


.footer-bottom
	padding: r(40px) 0
	
	.footer-item
		.footer-title
			font-size: r(18px)
			font-weight: 700
			line-height: r(24px)
			text-transform: uppercase
			margin-bottom: r(20px)
			display: flex
			flex-wrap: wrap
			span
				font-size: r(12px)
				margin-left: 3px
		+ .footer-item
			margin-top: r(30px)

		&.footer-info
			.footer-title
				margin-bottom: r(5px)
			a
				font-size: r(30px)
				text-transform: uppercase
				display: flex
				line-height: 1.2
				align-items: baseline
				em
					color: #ccc
					display: inline-block
					height: r(30px)
					position: relative
					top: 4px
				strong
					color: map-get($colors, main)
					margin: 0 0 0 6px
					display: flex
					flex-wrap: wrap
					align-items: baseline
				span
					font-weight: 700
					font-size: r(12px)
					color: map-get($colors, main)
					margin-left: 5px

		&.footer-social
			.footer-title
				margin-bottom: r(10px)
			ul
				display: flex
				li
					+ li
						margin-left: 15px

		&.footer-nav
			ul
				li
					a
						line-height: 1.4
						font-size: r(15px)
						color: #666
					+ li
						margin-top: r(10px)

	@media screen and ( max-width: 576px )
		.footer-item
			&.footer-info
				a
					font-size: 18px
					em
						top: 0

.footer-copyright
	background-color: #f8f8f8
	padding: r(30px) 0
	border-top: 1px solid #e5e5e5
	.row
		+ .row
			margin-top: 10px
	.company-info,.copyright,.web-term
		font-size: r(14px)
		line-height: r(20px)
		color: #666
	.bct
		text-align: right
	.copyright,.web-term
		color: #999
	.web-term
		text-align: right
		span
			margin: 0 5px
	@media screen and ( max-width: 768.98px )
		.web-term,.bct,.company-info,.copyright,.web-term
			text-align: left