.sub-banner-2
	.banner
		background: #000
		position: relative
		+img-scale(290 / 1920 * 100%)
		img
			opacity: 0.75
		.caption
			font-size: r(48px)
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
			color: #fff
			font-weight: 700
			padding: 0 15px
			text-align: center
	@media screen and ( max-width: 1024.98px )
		.banner
			height: 180px
			padding-top: 0
			img
				width: 100%
				height: 100%
				object-fit: cover
			.caption
				font-size: 24px
				white-space: nowrap