.contact-1
	.row
		border-radius: 10px
		overflow: hidden
		box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.05)

	.form-contact
		padding: r(50px)
		background-image: url(../img/contact/bg.png)
		background-repeat: no-repeat
		background-position: 110% 120%
		.form-group
			+ .form-group
				margin-top: r(30px)
			label
				display: inline-block
				font-weight: 700
				text-transform: uppercase
			input[type="text"]
				border: 0
				border-bottom: 1px solid #ebebeb
				height: 35px
				padding: 5px 12px
				font-size: 14px
				width: 100%
				background: transparent
				&::placeholder
					font-style: italic
					color: map-get($colors, light-grey)

		.frm-btn
			input[type=submit]
				width: r(160px)
				height: r(40px)
				border-radius: r(20px)
				background: $gradient-1
				color: #fff
				font-weight: 700
				border: 0
				cursor: pointer
	.form-info
		padding: 3.125rem 9.125rem 3.125rem 3.125rem
		.section-title
			margin-bottom: r(20px)
		ul
			li
				display: flex
				+ li
					margin-top: 15px
				.icon
					width: 50px
				.text
					flex: 1
					line-height: 1.375
					* + *
						margin-top: 5px
					p
						strong
							text-transform: uppercase
	@media screen and ( max-width: 1024.98px )
		.col-lg-7
			order: 2
		.col-lg-5
			order: 1
		.form-contact
			padding: r(30px)
		.form-info
			padding: r(30px)
	@media screen and ( max-width: 768.98px )
		padding: 0 !important
		.row
			border-radius: 0
		.container
			padding: 0
			max-width: none
		.form-contact
			.frn-btn
				input[type=submit]
					width: 100%

.contact-2
	.contact-map
		iframe
			height: 550px
			width: 100%
	@media screen and ( max-width: 768.98px )
		display: none