header:not(.checkout-header)
	position: relative
	top: 0
	left: 0
	width: 100%
	background: #fff
	.top-header
		background: #fafafa
		padding: 10px 0
		border-bottom: 1px solid #f2f2f2
	.middle-header
		.row
			margin-bottom: 0
			> *
				padding-bottom: 0
	.logo,.vn-pay-banner
		transition: 0.3s all
		max-width: 180px
	.menu
		ul
			li
				+ li
					margin-left: 15px
					padding-left: 15px
					position: relative
					&:before
						position: absolute
						content: ''
						top: 0
						left: 0
						bottom: 0
						width: 1px
						background: #cccccc
				a
					font-size: 13px
					min-height: 20px
					display: flex
					align-items: center
					em
						display: none

	.header-util-list
		.header-util-item
			min-height: 20px
			display: flex
			align-items: center
			+ .header-util-item
				margin-left: 15px
				padding-left: 15px
				position: relative
				&:before
					position: absolute
					content: ''
					top: 0
					left: 0
					bottom: 0
					width: 1px
					background: #cccccc

	.hotline
		a
			em
				color: map-get($colors, main)
				font-size: 18px
				position: relative
				top: 2px
			strong
				font-weight: 800
				font-size: 20px
				color: map-get($colors, main)
			span
				font-size: 14px

	.language
		.ri-arrow-drop-down-fill
			color: #cccccb
		.dropdown-content
			padding-top: 10px
			min-width: 51px
			right: auto
			left: 50%
			transform: translate(-50%)
			ul
				padding: 15px
				li
					+ li
						margin-top: 10px
	
	.order-check
		a
			font-size: 14px
			em
				font-size: 18px
				color: #999
				margin-right: 5px

	.account
		.firstnav
			> span
				font-weight: 700
				white-space: nowrap
		a
			font-size: 14px
			white-space: nowrap
			span
				margin: 0 0 0 5px
				width: 90px
				overflow: hidden
				text-overflow: ellipsis
			em
				font-size: 18px
				color: #cccccb
		.dropdown-content
			padding-top: 10px
			right: auto
			left: 50%
			transform: translate(-50%)
			ul
				li
					+ li
						margin-top: 10px
	
	.middle-header
		padding: 20px 0
		transition: 0.3s all

	.search-wrapper
		padding-left: r(45px)
		.searchbox
			display: flex
			align-items: center
			width: 100%
			position: relative
			input
				height: 40px
				flex: 1
				border: 2px solid map-get($colors, main)
				padding: 0 15px
				font-size: 14px
				border-radius: 4px 0 0 4px
				&::placeholder
					color: #999
			button
				height: 40px
				width: 45px
				background: map-get($colors, main)
				border: 0
				color: #fff
				font-size: 24px
				display: flex
				align-items: center
				justify-content: center
				cursor: pointer
				border-radius: 0 4px 4px 0
		.suggestsearch
			position: absolute
			top: 100%
			padding: 20px
			background: #f9f9f9
			border-radius: 10px
			border: 1px solid #eeeeee
			box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.15)
			margin-top: 10px
			z-index: 10
			display: none
			.keyword-list
				line-height: 1.375
				ul
					li
						+ li
							margin-top: 5px
						a
							font-size: r(15px)
							color: #666
			.product-suggest-list
				> .row
					margin-left: r(-20px)
					margin-bottom: r(-20px)
					> *
						padding-left: r(20px)
						padding-bottom: r(20px)
				.product-item
					background: #fff
					border: 1px solid #eeeeee
					border-radius: 5px
					padding: 10px
					.product-name
						&:hover
							text-decoration: underline
					.product-price 
						.price
							font-size: r(14px)
						.old-price 
							font-size: r(12px)
					.product-vnpay-price
						font-size: r(12px)
			.view-more-wrap
				text-align: center
				margin-top: 20px
				.btn
					height: 40px
					width: 220px
					border: 2px solid map-get($colors, main)
					color: map-get($colors, main)
					border-radius: 20px

	.bottom-header
		padding-bottom: 10px

	.site-feature-wrapper
		width: calc(100% - 250px)

	.site-feature
		ul
			display: flex
			align-items: center
			li
				+ li
					padding-left: r(40px)
				a
					display: flex
					align-items: center
					span
						margin-left: 5px
						font-size: 14px
						color: map-get($colors, blue)

	.cart
		text-align: center
		position: relative
		.cart-toggle
			cursor: pointer
			.cart-amount
				width: 20px
				height: 20px
				top: -5px
				right: -8px
		.cart-dropdown
			position: absolute
			top: 100%
			right: 0
			width: r(360px)
			background: #fff
			border-radius: 10px
			box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.05)
			margin-top: 10px
			z-index: 20
			display: none
			&.show
				display: block

		.cart-container
			padding: r(25px)

		.cart-heading
			text-align: right
			padding-bottom: r(12px)
			border-bottom: 3px solid #ebebeb
			margin-bottom: r(15px)

		.cart-body
			overflow-y: auto
			height: r(265px)
			padding-right: 15px
			&::-webkit-scrollbar
				width: 4px
				background-color: #ccc
			&::-webkit-scrollbar-thumb
				background-color: map-get($colors, blue)
			ul
				li
					+ li
						margin-top: 15px
						padding-top: 15px
						border-top: 1px solid #ebebeb
		
		.cart-product
			.img
				display: block
				width: r(60px)
			.caption
				flex: 1
				text-align: left
				padding-left: r(20px)
			.name
				display: block
				margin-bottom: 10px
				&:hover
					color: map-get($colors, main)
			.quantity
				color: #999

		.cart-button
			margin-top: r(15px)
			padding-top: r(15px)
			border-top: 1px solid #ebebeb
			a
				height: r(40px)
				width: 100%
				background: linear-gradient(90deg, rgba(240,90,148,1) 0%, rgba(188,0,73,1) 100%)
				color: #fff
				border-radius: 20px
				em
					margin-left: r(15px)


	
	@media screen and ( max-width: 1280.98px )
		.site-feature
			ul
				li
					+ li
						padding-left: r(20px)
		.menu
			ul
				li
					+ li
						margin-left: 10px
						padding-left: 10px
					a
						font-size: 12px

		.hotline
			a
				em
					font-size: 14px
				strong
					font-size: 16px
				span
					font-size: 12px



	@media screen and ( max-width: 1024.98px )
		height: 60px
		.top-header,.bottom-header
			display: none !important

		.middle-header
			background-color: map-get($colors, main)
			padding: 10px 0
			.row
				margin-bottom: 0
				margin-left: 0
				> *
					padding: 0
					max-width: none
				.col-lg-2
					flex: 0 0 30px
				.col-lg-1
					flex: 0 0 28px
				.col-lg-7
					flex: 1 1 0%

		.menu-mobile-toggle
			font-size: 30px
			color: #fff
			display: flex
			align-items: center

		.product-detail-back-btn,.product-detail-menu
			display: none

		.search-wrapper
			padding: 0 15px
			.searchbox
				position: relative
				button
					position: absolute
					left: 5
					top: 50%
					transform: translateY(-50%)
					background: transparent
					color: map-get($colors, main)
					font-size: 20px
				input
					padding: 0 20px 0 40px 
			.suggestsearch
				position: fixed
				top: 70px
				width: 100%
				margin-top: 0
				left: 0
				bottom: 0
				border-radius: 0
				z-index: 100
				height: calc(100vh - 70px)
				overflow: auto
				padding: 15px
				.product-suggest-list
					margin-top: 20px
		.cart
			.cart-toggle
				img
					filter: brightness(0) invert(1)
				.cart-amount
					background-color: map-get($colors, blue)

		.menu
			.icon-menu
				display: none
			ul
				flex-direction: column
				align-items: flex-start
				width: 100%
				li
					width: 100%
					+ li
						padding-left: 0
						margin-left: 0
						&:before
							display: none
					a
						min-height: 40px
						padding: 0 30px
						font-size: 15px
						border-top: 1px solid #e5e5e5
						em
							display: block
							margin-right: 8px
							color: #999

		.product-category-wrapper
			padding: 0 30px
			.product-category-toggle
				color: #333
				strong
					font-size: 15px
					font-weight: normal
				em
					font-size: 15px
					color: #999
				.ri-arrow-drop-down-fill
					display: none
			.product-category-dropdown
				display: none !important

		.language
			font-size: 15px
			margin-top: 20px
			.current
				span
					width: 100px
				
		.hotline
			display: flex
			align-items: center
			margin-top: 20px
			span
				width: 100px
				font-size: 15px
			a
				strong
					font-size: 20px
				em
					display: none

		
	
	@media screen and ( max-width: 768.98px )
		.menu
			ul
				li
					a
						padding: 0 15px
		.product-category-wrapper
			padding: 0 15px

.mobile-menu
	position: absolute
	left: 0
	top: 100%
	width: 100%
	display: none
	z-index: 99
	.mobile-menu-wrap
		background-color: #fff
	.mobile-menu-tool
		padding: 20px 30px
		border-top: 5px solid #e5e5e5
		display: flex
		flex-direction: column-reverse
	@media screen and ( max-width: 768.98px )
		background: #fff
		.container
			padding: 0
		.mobile-menu-tool
			padding: 20px 15px

.homepage
	header
		.bottom-header
			display: none

.backdrop
	position: fixed
	z-index: 98
	top: 60px
	bottom: 0
	left: 0
	right: 0
	background-color: rgba(#000 , 0.3)
	display: none

.sticky-wrapper
	&:not(.is-sticky)
		height: auto !important

	&.is-sticky
		header
			box-shadow: 0 0 15px 0 rgba(18,51,104,.1)
			@media (min-width: 1024.98px)
				.middle-header
					padding: 10px 0
				.logo,.vn-pay-banner
					max-width: 120px
					transition: 0.3s all
				.vn-pay-banner
					margin-left: auto

.product-detail-page
	@media screen and ( max-width: 1024.98px )
		.menu-mobile-toggle
			display: none !important
		.search-wrapper
			display: none !important
		.product-detail-back-btn
			font-size: 30px
			color: #fff
			display: flex !important
			align-items: center
		.product-detail-menu
			display: block !important
			ul
				display: flex
				justify-content: flex-end
				li
					a
						font-size: 30px
						color: #fff
						display: flex
						align-items: center
						margin: 0 10px
		.fixed-nav-mobile
			display: none !important