.product-category
	width: 250px

	.product-category-toggle
		color: map-get($colors, blue)
		height: 40px
		cursor: pointer
		padding-left: 20px
		em
			font-size: 24px
		strong
			margin: 0 5px

	.product-category-dropdown
		left: 0
		right: 0
		border: 1px solid #e5e5e5
		z-index: 20

	.product-category-list
		padding: 4px 0
		> ul
			> li
				padding: 0 20px
				border-right: 0
				transition: 0.3s all
				+ li
					.product-category-link
						border-top: 2px dotted #e5e5e5
				@media screen and ( min-width: 1024px )
					&:not(:first-child)
						&:hover
							box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1)
							.product-category-link
								color: map-get($colors, main)
								.arrow
									color: map-get($colors, main)
								&:before
									background: #fff
								&:after
									border: 1px solid map-get($colors, main)
									border-right: 3px solid #fff
							.text
								.icon
									img
										filter: invert(50%) sepia(10%) saturate(6184%) hue-rotate(302deg) brightness(99%) contrast(87%)
							.product-category-subnav-container
								display: block

	.product-category-link
		min-height: 40px
		transition: 0.3s all
		position: relative
		&:before
			content: ''
			position: absolute
			right: -21px
			top: -1px
			bottom: 0
			background-color: transparent
			width: 1px
			z-index: 2
		&:after
			position: absolute
			content: ''
			top: -1.5px
			bottom: -0.5px
			left: -20px
			right: -23px
			pointer-events: none
			z-index: 3
		.text
			.icon
				width: 24px
				text-align: center
				img
					transition: 0.3s all
			span
				flex: 1
				padding-left: 5px
				font-size: 14px
		.arrow
			color: #ccc
			font-size: 16px

	.product-category-subnav-container
		position: absolute
		background: #fff
		left: 100%
		margin-left: 1px
		top: -41px
		bottom: -1px
		border: 1px solid map-get($colors, main)
		padding: 20px
		overflow: auto
		display: none
		box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05)
		z-index: 2
		width: 980px
		&::-webkit-scrollbar
			width: 5px
			background: #ccc
		&::-webkit-scrollbar-thumb
			background: map-get($colors, blue)


	.product-category-subnav-row
		display: flex

	.product-category-subnav-groups
		column-count: 4
		column-gap: 10px
		width: calc( 100% - 98px )

	.product-category-subnav
		line-height: 1.25
		break-inside: avoid
		margin-bottom: 25px
		.product-category-lv-2
			font-size: 15px
			font-weight: 700
			margin-bottom: 5px
			&:hover
				color: map-get($colors, main)
		.product-category-lv-3
			ul
				li
					+ li
						margin-top: 5px
					a
						font-size: 13px
						&:hover
							color: map-get($colors, main)

	.product-category-brand
		width: 98px
		ul
			li
				a
					display: flex
					align-items: center
					justify-content: center
					height: 50px
					background: #f5f5f5
					img
						max-height: 40px
				+ li
					margin-top: 2px
	@media screen and ( max-width: 1024.98px )
		.product-category-toggle
			padding-left: 0
	