.news-item
	border-radius: 5px
	overflow: hidden
	background: #fff
	box-shadow: 0px 0px 15px 0px rgba(18, 51, 104, 0.08)
	display: flex
	flex-direction: column
	height: 100%
	.news-caption
		padding: r(20px)

	.news-title
		a
			&:hover
				color: map-get($colors, main)

	.news-view-more
		&:hover
			color: map-get($colors, main)

	.news-brief
		+line(3)

	.news-image
		overflow: hidden
		img
			transition: 0.3s all
		&:hover
			img
				transform: scale(1.125)

	&.big-news
		.news-image
			a
				+img-scale(370 / 600 * 100%)

	&.small-news
		.news-image
			a
				+img-scale(176 / 285 * 100%)

	&.side-news
		flex-direction: row
		.news-image
			width: 270 / 600 * 100%
			height: 167px
			img
				width: 100%
				height: 100%
				object-fit: cover
		.news-caption
			flex: 1 1 0%
			padding: 15px 20px
		.news-title
			a
				+line(2)
		.news-brief
			+line(2)

	@media screen and ( max-width: 576px )
		&.side-news
			flex-direction: column
			.news-image
				height: auto
				width: 100%
				a
					+img-scale(176 / 285 * 100%)