#tracking-order
	padding: r(30px)
	width: 800px
.tracking-order-form
	.form-group
		label
			display: block
			line-height: 1.375
			font-size: r(22px)
			text-align: center
			margin-bottom: r(15px)
			font-weight: 700
		input
			line-height: 1.21428571em
			padding: .67857143em 1em
			font-size: 1em
			background: #fff
			border: 1px solid rgba(34,36,38,.15)
			color: rgba(0,0,0,.87)
			border-radius: .28571429rem
			width: 100%
	.frm-btn
		text-align: center
		a
			display: inline-flex
			align-items: center
			justify-content: center
			font-weight: 700
			height: r(40px)
			background: $gradient-1
			color: #fff
			border-radius: r(25px)
			padding: 0 15px
			margin-top: 20px